module.exports = ".toast {\n    position: relative;\n    display: flex;\n    justify-content: space-around;\n    margin-bottom: 20px;\n    font-size: 90%;\n    padding: 14px; \n    width: auto;\n    word-wrap: none;\n    overflow: hidden;\n    background: var(--main-color);\n    box-shadow: var(--box-shadow);\n    cursor: pointer;\n    transition: all .3s;\n    border-radius: 10px;\n    border: 3px transparent;\n}\n\n.success {\n    border: 3px solid var(--success-color)!important;\n}\n\n.danger {\n    border: 3px solid var(--danger)!important;\n}\n\n.warning {\n    border: 3px solid var(--warning)!important;\n}\n\n.toast p {\n    margin: 0;\n}"