module.exports = ".card{\n    border: none!important;\n    background-color: var(--main-color);\n    margin: 100px auto;\n    max-width: 500px;\n    text-align: center;\n    box-shadow: var(--box-shadow);\n    transition: all 500ms;\n  }\n\n  .card-title h2 {\n    letter-spacing: 1px;\n    color: white;\n  }\n\n  .card-title img {\n    width: 75%;\n  }\n\n  .card-body{\n    padding: 35px 70px;\n  }\n\n  input, button {\n      margin-top: 0.7em;\n      font-size: 85%;\n  }\n\n  input {\n  background: white!important;\n  display: inline;\n  color: var(--darker-font-color)!important;\n  border: 1px solid var(--font-subcolor)!important;\n}\n\n  @media only screen and (max-width: 768px) {\n    .card {margin: 45px auto;}\n    .card-body{padding: 35px 110px;}\n    small{font-size: 75%;}\n}\n\n  @media only screen and (max-width: 425px) {\n      .card {margin: 45px auto;border-radius: 0;}\n      .card-body{padding: 35px 55px;}\n      .card-title h2 {font-size:20px;}\n      small{font-size: 65%;}\n  }\n\n  \n\n\n\n"