module.exports = ".wrapper {\n  position: relative;\n  width: 100%;\n}\n\n.responsive {\n  display: none;\n}\n\n.sidebar-panel{\n  overflow-x: hidden;\n  background-color: var(--secondary-color);\n  width: 200px;\n  position: absolute;\n  height: 100%;\n  min-height: 100vh;\nbox-shadow: 13px 0px 45px -16px rgba(0,0,0,0.75);\n  transition: all 500ms;\n  z-index: 998;\n}\n\n.main-panel{\n  width: 100%;\n  flex: 1;\n  padding: 20px 20px 20px 220px; /*top right bottom left*/\n  transition: all .5s;\n}\n\n.page-title {\n  font-size: 1.1375rem;\n  font-weight: 700!important;\n  line-height: 1.5;\n  text-transform: uppercase;\n  letter-spacing: 1.01px;\n  margin-bottom: 20px;\n}\n\n@media only screen and (max-width: 768px) {\n  .responsive {\n    display: block;\n  }\n\n  .main-panel{\n    padding: 20px 20px 20px 20px; /*top right bottom left*/\n  }\n\n  .page-title {\n    text-align: center;\n  }\n\n}\n\n#sidebarBtn {\n  position: absolute;\n  top: 6.5px;\n  left: 6.5px;\n  z-index: 99;\n  font-size: 18px;\n  border: none;\n  outline: none;\n  background-color: var(--secondary-color);\n  border-radius: 100%;\n  color: white;\n  cursor: pointer;\n  padding: 10px 15px;\n  box-shadow: 0 8px 10px -1px rgba(0, 0, 0, 0.42);\n\n  transition: all 500ms;\n}\n\n.btnChangePosition {\n  left: 11.5em!important;\n}\n\n/*-------Loading-Screen------*/\n\n.loading {\n  position: fixed;\n  z-index: 999;\n  height: 2em;\n  width: 2em;\n  overflow: show;\n  margin: auto;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n}\n\n/* Transparent Overlay */\n\n.loading:before {\n  content: '';\n  display: block;\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n    background: radial-gradient(rgba(20, 20, 20,.8), rgba(0, 0, 0, .8));\n\n  background: -webkit-radial-gradient(rgba(20, 20, 20,.8), rgba(0, 0, 0,.8));\n}\n\n/* :not(:required) hides these rules from IE9 and below */\n\n.loading:not(:required) {\n  /* hide \"loading...\" text */\n  font: 0/0 a;\n  color: transparent;\n  text-shadow: none;\n  background-color: transparent;\n  border: 0;\n}\n\n.loading:not(:required):after {\n  content: '';\n  display: block;\n  font-size: 10px;\n  width: 1em;\n  height: 1em;\n  margin-top: -0.5em;\n  -webkit-animation: spinner 1500ms infinite linear;\n  animation: spinner 1500ms infinite linear;\n  border-radius: 0.5em;\nbox-shadow: rgba(255,255,255, 0.75) 1.5em 0 0 0, rgba(255,255,255, 0.75) 1.1em 1.1em 0 0, rgba(255,255,255, 0.75) 0 1.5em 0 0, rgba(255,255,255, 0.75) -1.1em 1.1em 0 0, rgba(255,255,255, 0.75) -1.5em 0 0 0, rgba(255,255,255, 0.75) -1.1em -1.1em 0 0, rgba(255,255,255, 0.75) 0 -1.5em 0 0, rgba(255,255,255, 0.75) 1.1em -1.1em 0 0;\n}\n\n/* Animation */\n\n@-webkit-keyframes spinner {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes spinner {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n"